/* Start::app-header */
.app-header {
  max-width: 100%;
  height: 4.25rem;
  z-index: 100;
  position: fixed;
  inset-block-start: 0;
  inset-inline: 0;
  background: $header-bg;
  border-block-end: 1px solid $header-border-color;
  transition: all 0.1s ease;
  box-shadow: 0px 2px 0px rgba(118, 138, 254, 0.03);

  #mainHeaderProfile {
    color: $header-prime-color;
  }
}

@media (min-width: 992px) {
  .app-header {
    padding-inline-start: 16rem;
  }
}

.header-icon-badge {
  position: absolute;
  padding: 0.15rem 0.25rem;
  inset-block-start: 8px;
  inset-inline-end: 5px;
  font-size: 0.625rem;
}

.header-icon-pulse {
  position: absolute;
  width: 5px;
  height: 5px;
  inset-block-start: 16px;
  inset-inline-end: 11px;
}

.related-app {
  text-align: center;
  border-radius: $default-radius;
  border: 1px solid $default-border;

  &:hover {
    background-color: $light;
  }
}

.header-profile-dropdown {
  min-width: 11rem;
}

/* End::app-header */

/* Start::main-header-container */
.main-header-container {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  height: 100%;
  border-bottom: 0px solid $input-border;

  .header-content-left,
  .header-content-right {
    display: flex;
    align-items: stretch;
    margin-block: auto;
  } 
  .header-content-right {
    padding-inline-start: 0 !important;
  }
  .header-element {
    display: flex;
    align-items: stretch;
  }

  .header-link {
    display: flex;
    align-items: center;
    padding: 0 0.4rem;

    &:hover,
    &.show {
      .header-link-icon {
        fill: $header-prime-color;
        color: $header-prime-color;
      }
    }
  }

  .header-link-icon {
    // width: 2.125rem;
    // height: 2.9rem;
    font-size: 1.2rem;
    color: $header-prime-color;
    fill: $header-prime-color;
    background-color: transparent;
    box-shadow: 0px 2px 0px rgba(118, 138, 254, 0.03);
    transition: all ease 0.05s;
    position: relative;
    // padding: 0.75rem;
    padding: 0.5rem;
    border-radius: 50%;
    margin: 5px 0px;
    // border: 1px solid $default-border;
  }

  .dropdown-toggle {
    text-decoration: none;

    &::after {
      content: none;
    }
  }

  .main-profile-user {
    .dropdown-menu {
      width: 13rem;
    }

    .dropdown-item {
      font-weight: 400;
      font-size: 0.813rem;
      color: $default-text-color;
      padding: 1.25rem 1rem;
      height: 2.375rem;
      display: flex;
      align-items: center;

      &:hover {
        color: $primary;

        i {
          color: $primary;
          opacity: 1;
        }
      }
    }
  }

  .main-header-dropdown {
    box-shadow: $box-shadow;
    inset-block-start: .25rem !important;
    li {
      border-bottom: 1px solid $default-border;
      &:last-child {
        border-bottom: 0;
      }
    }
    .dropdown-item {
      padding: 0.6rem 1rem;
      &:last-child {
        border-block-end: 0;
      }
    }
  }

  .cart-dropdown .main-header-dropdown {
    width: 22rem;
  }

  .notifications-dropdown .main-header-dropdown {
    width: 18rem;
  }
  .messages-dropdown .main-header-dropdown {
    width: 21rem;
  }
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(6rem);
  }

  100% {
    opacity: 1;
  }

  0% {
    opacity: 0;
    transform: translateY(6rem);
  }
}

[dir="rtl"] {
  .sidemenu-toggle {
    .open-toggle {
      transform: rotate(180deg);
    }
  }
  .app-header {
    .dropdown-menu-end {
      --bs-position: end;
    }
  }
}

/* End::main-header-container */

/* Start::Header-dropdown */
.header-product-item {
  padding-inline-start: 0;
  display: flex;

  li {
    position: relative;
    list-style-type: none;
    font-size: 0.75rem;
    font-weight: 400;
    color: $text-muted;
    padding-inline-end: 0.938rem;
  }

  li:nth-child(2),
  li:nth-child(3),
  li:nth-child(4) {
    &:before {
      position: absolute;
      content: "";
      inset-inline-start: -0.438rem;
      width: 1px;
      inset-block-start: 0.25rem;
      height: 0.625rem;
      background-color: $text-muted;
      opacity: 0.1;
    }
  }
}

