}
/* End::Header-dropdown */

/* Start::header-search */
#searchModal {
  .form-control {
    position: relative;
  }

  .input-group {
    border: 2px solid $primary;
    border-radius: 0.25rem;

    i {
      color: $text-muted;
    }
  }
}

.header-search {
  position: relative;
}
.autoComplete_wrapper > {
  .header-search-bar {
    position: relative;
    min-width: 20rem !important;
    border-radius: 0.5rem;
    padding: .375rem 2rem .375rem .75rem !important;
    border: 1px solid $default-border !important;
    background-color: transparent;
    &:focus {
      border: 1px solid $default-border !important;
      background-color: transparent;
    }
  }
}

.header-search-icon {
  position: absolute;
  top: .4325rem;
  inset-inline-end: .75rem;
  color: $text-muted;
}
.header-profile-img {
  width: 2rem;
  height: 2rem;
}

/* End::header-search */

